<template>
<v-layout row wrap>    
<v-flex xs12>
<h3 class="text-md-center featuredApparelsHead">Featured Apparels</h3>
</v-flex>
<v-flex v-for="(item,i) in items" :key="`${i}`" xs4>
<div>
    <img class="img-fluid" alt="" v-bind:src="item.src"/>
</div>
</v-flex>
</v-layout>
</template>
<script>
  export default {
    props: ['items']
  }
</script>



